<template>
  <div class="mt-2">
    <div v-for="p in passages" class="row">
      <passage-item :item="p" class="col-sm-12 col-md-10 col-lg-10 mx-auto"></passage-item>
    </div>
  </div>
</template>

<script>
import PassageItem from "./PassageItem";
export default {
  name: "PassageList",
    components: {PassageItem},
    data(){
      return{
        passages:[]
      }
    },
    mounted() {
      this.getPassageList()
    },
    methods:{
      getPassageList() {
        const query = Bmob.Query("PassageList");
        query.limit(10);
        query.order("-createdAt");
        query.find().then(res => {
          this.passages = res
        });
      }
    }
}
</script>

<style scoped>

</style>



