<template>
  <div class="container mt-3">
    <div class="row">
      <div class="col-sm-12 col-md-12 col-lg-4 pt-4" style="height: fit-content">
        <el-upload
          action=""
          list-type="picture-card"
          :http-request="uploadCover"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
          <img width="100%" :src="dialogImageUrl" alt="">
        </el-dialog>
        <el-link :href="coverlink" target="_blank" :underline="false" type="success">封面链接: {{ coverlink }}</el-link>
        <div class="input-group mb-3">
          <input type="text" class="form-control" v-model="ptitle" placeholder="标题">
        </div>
        <div class="input-group mb-3">
          <textarea style="height: 200px" type="text" class="form-control" v-model="description" placeholder="简介"/>
        </div>

      </div>
      <div class="col-sm-12 col-md-12 col-lg-8 text-right" style="z-index: 0">
        <div class="btn-group m-2" role="group">
          <div class="btn btn-bg">
            <div class="svg-align">
              <svg t="1619178963987" class="icon" viewBox="0 0 1553 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
                   p-id="11896" width="26" height="26">
                <path
                  d="M1057.227034 0.105931A487.600552 487.600552 0 0 0 709.737931 146.39669a331.917241 331.917241 0 0 0-118.324965-21.186207 362.425379 362.425379 0 0 0-339.544276 257.05931 307.764966 307.764966 0 0 0-241.558069 234.036966 346.712276 346.712276 0 0 0 56.743724 288.909241 289.227034 289.227034 0 0 0 230.929655 118.360276h378.385655a30.649379 30.649379 0 0 0 30.649379-30.649379v-134.179311a30.790621 30.790621 0 0 0-30.967172-30.684689c-121.467586 0.670897-138.310621 4.201931-165.958621-24.081656a78.353655 78.353655 0 0 1 1.271173-109.885793c264.827586-264.827586 256.953379-267.934897 300.314483-267.934896s35.84 3.001379 300.420413 267.970207a78.283034 78.283034 0 0 1-45.903448 133.225931v0.600276h-128.635586a30.68469 30.68469 0 0 0-30.68469 30.684689v134.426483a30.649379 30.649379 0 0 0 30.68469 30.649379h92.19531c9.110069 0.317793 18.220138 0.600276 27.330207 0.600276 9.675034 0 19.703172-0.282483 29.095724-0.918069a504.055172 504.055172 0 0 0 467.014621-484.669793c0.317793-8.509793 0.317793-17.655172 0.317793-26.412138A504.937931 504.937931 0 0 0 1057.227034 0.105931z"
                  p-id="11897" fill="#ff6b6b"></path>
              </svg>
            </div>
            <div class="btn-align">
              保存
            </div>
          </div>
          <div class="btn btn-bg" @click="sendpassage">
            <div class="svg-align">
              <svg t="1619179317929" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
                   p-id="16574" width="26" height="26">
                <path
                  d="M188.714667 774.570667H128a42.666667 42.666667 0 0 1-42.666667-42.666667V149.333333a42.666667 42.666667 0 0 1 42.666667-42.666666h582.570667a42.666667 42.666667 0 0 1 42.666666 42.666666v60.714667H231.381333a42.666667 42.666667 0 0 0-42.666666 42.666667v521.856z"
                  fill="#82C4FF" p-id="16575"></path>
                <path
                  d="M637.994667 645.994667l-61.461334 54.016a21.333333 21.333333 0 0 0 28.16 32.042666l104.106667-91.456a21.333333 21.333333 0 0 0-0.042667-32.064l-104.085333-91.264a21.333333 21.333333 0 1 0-28.117333 32.064l61.568 53.994667h-186.304a21.333333 21.333333 0 1 0 0 42.666667h186.176zM874.666667 918.421333H292.096a42.666667 42.666667 0 0 1-42.666667-42.666666V313.429333a42.666667 42.666667 0 0 1 42.666667-42.666666H874.666667a42.666667 42.666667 0 0 1 42.666666 42.666666v562.346667a42.666667 42.666667 0 0 1-42.666666 42.666667z"
                  fill="#0287FF" p-id="16576"></path>
              </svg>
            </div>
            <div class="btn-align">
              发布
            </div>
          </div>
        </div>
        <div id="toolbar"></div>
        <div id="editorarea" class="mb-2"></div>
      </div>
    </div>

  </div>
</template>

<script>
import E from 'wangeditor'
import * as qiniu from 'qiniu-js'
import SparkMD5 from 'spark-md5'
export default {
  name: "Edit",
  data() {
    return {
      editor: null,
      thtml: null,
      reshtml: "",
      ptitle: "",
      coverlink: "",
      description: "",
      webtocken:"",
      dialogImageUrl: '',
      dialogVisible: false
    }
  },
  mounted() {
    this.getTocken()
    this.editor = new E("#toolbar", "#editorarea")
    this.editor.config.height = 490
    this.editor.config.uploadImgMaxSize = 20 * 1024 * 1024;
    this.editor.config.uploadImgMaxLength = 1
    var _this = this
    this.editor.config.customUploadImg = function (resultFiles, insertImgFn){
      // resultFiles 是 input 中选中的文件列表
      // insertImgFn 是获取图片 url 后，插入到编辑器的方法
      _this.uploadImg(resultFiles[0],insertImgFn)
    }
    this.editor.create()
  },
  methods: {
    uploadCover(files){
      this.uploadImg(files.file,(imgUrl)=>{
        this.coverlink = imgUrl
      })
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    getTocken(){
      this.axios.get('https://qnt.cotubo.cn:7443/vuepage').then((response) => {
        this.webtocken = response.data
      })
    },
    checkPassage(){
      if (this.coverlink!=""&&this.ptitle!=""&&this.getPassage()!=""){
        return true
      }
      return false
    },
    savepassage() {
      const query = Bmob.Query('DraftPassage');
      query.set("name", "Bmob")
      query.set("cover", "后端云")
      query.save().then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
    },
    sendpassage() {
      if (this.checkPassage()){
        const passageq = Bmob.Query('Passage');
        passageq.set("content", this.getPassage())
        passageq.save().then(res => {
          console.table(res)
          const pointer = Bmob.Pointer('Passage')
          const passage = pointer.set(res.objectId)
          const listq = Bmob.Query('PassageList');
          listq.set("title", this.ptitle)
          listq.set("description", this.description)
          listq.set("coverlink", this.coverlink)
          listq.set("passage", passage)
          listq.save().then(res => {
            alert("发布成功")
          }).catch(err => {
            passageq.destroy(res.objectId).then(res => {
              console.table(res)
            }).catch(err => {
              console.table(err)
            })
            console.table(err)
          })
        }).catch(err => {
          console.table(err)
        })
      }else {
        alert("填写信息过少")
      }
    },
    getPassage() {
      return this.editor.txt.html()
    },
    uploadImg(file, callback) {
      var file_name = file.name+""
      var fnindex = file_name.lastIndexOf(".")
      var prefix = file_name.substring(0,fnindex)
      var suffix = file_name.substring(fnindex,file_name.length)
      var key = SparkMD5.hash(prefix)+suffix;
      var domain = "http://qiniu.cotubo.cn/"; //你的七牛空间域名
      var config = {
        useCdnDomain: true,
        disableStatisticsReport: false,
        retryCount: 6,
        region: qiniu.region.z2
      };
      var putExtra = {
        fname: "",
        params: {},
        mimeType: null
      };
      var observer = {
        next(res) {
          console.log("已上传：" + res.total.percent);
        },
        error(err) {
          console.log("上传发生问题：");
          console.log(err);
          alert("上传失败")
        },
        complete(res) {
          callback(domain + res.key)
        }
      }

      var observable = qiniu.upload(file, key, this.webtocken, putExtra, config);
      observable.subscribe(observer);
    }
  }
}
</script>

<style scoped>
.avatar-uploader {
  text-align: center;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409EFF;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.btn-bg {
  vertical-align: middle;
  background-color: #edf2fb;
}
.btn-bg:hover{
  background-color: #abc4ff;
}
.svg-align{
  vertical-align: top;
  display: inline-block;
}
.btn-align{
  display: inline-block;
  padding-top: 4px;
}
#editorarea{
  cursor: text;
  text-align: left;
  border: 1px solid #ccc;
  height: 490px;
}
</style>



