<template>
  <div>
    <h3>文章管理</h3>
    <table class="table table-responsive-sm table-responsive-md table-borderless table-hover">
      <tr v-for="(passage,index) in passages" class="row">
        <td class="col-sm-12 col-md-10 col-lg-10">
          <passage-item :item="passage"></passage-item>
        </td>
        <td class="del col-sm-12 col-md-2 col-lg-2" style="line-height: 160px;text-align: center" id="liveToastBtn" @click="deleteRow(index,passage)">
          <div class="mx-auto d-block">
            <svg t="1619187555072" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
                 p-id="17378" width="48" height="48">
              <path
                d="M236.307692 236.307692v685.292308a23.630769 23.630769 0 0 0 23.63077 23.630769h504.123076a23.630769 23.630769 0 0 0 23.63077-23.630769V236.307692H236.307692z m393.846154-78.76923v-55.138462a23.630769 23.630769 0 0 0-23.630769-23.630769H417.476923a23.630769 23.630769 0 0 0-23.630769 23.630769v55.138462h236.307692z m78.769231 0h275.692308a39.384615 39.384615 0 1 1 0 78.76923h-118.153847v708.923077c0 43.504246-35.264985 78.769231-78.76923 78.769231H236.307692c-43.504246 0-78.769231-35.264985-78.76923-78.769231V236.307692H39.384615a39.384615 39.384615 0 0 1 0-78.76923h275.692308V78.769231c0-43.504246 35.264985-78.769231 78.769231-78.769231h236.307692c43.504246 0 78.769231 35.264985 78.769231 78.769231v78.769231zM433.230769 393.846154a39.384615 39.384615 0 0 1 39.384616 39.384615v315.076923a39.384615 39.384615 0 0 1-78.769231 0V433.230769a39.384615 39.384615 0 0 1 39.384615-39.384615z m157.538462 0a39.384615 39.384615 0 0 1 39.384615 39.384615v315.076923a39.384615 39.384615 0 0 1-78.769231 0V433.230769a39.384615 39.384615 0 0 1 39.384616-39.384615z"
                fill="#d81e06" p-id="17379"></path>
            </svg>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import PassageItem from "./PassageItem";

export default {
  name: "PassageManager",
  components: {PassageItem},
  data() {
    return {
      passages: []
    }
  },
  mounted() {
    this.getPassageList()
  },
  methods: {
    deleteRow(index, passage) {
      var del = confirm("确定要删除么？");
      if (del == true) {
        const PassageList = Bmob.Query('PassageList');
        PassageList.destroy(passage.objectId).then(res => {
          const PassageTable = Bmob.Query('Passage');
          PassageTable.destroy(passage.passage.objectId).then(res => {
            this.getPassageList()
          }).catch(err => {
            console.log(err)
          })
        }).catch(err => {
          console.log(err)
        })
      } else {

      }
    },
    getPassageList() {
      const query = Bmob.Query("PassageList");
      query.limit(10);
      query.order("-createdAt");
      query.find().then(res => {
        this.passages = res
      });
    }
  }
}
</script>

<style scoped>
.del:hover {
  background-color: #ff6b6b;
  color: white;
}
</style>



